.customReg{
	position: fixed;
	z-index: 14;
	opacity: 0;
	left:20px;
	bottom:50px;
	background:#04423c url(../images/customReg.png) 10px center no-repeat;
	background-size:39px;
	padding:10px 15px;
	padding-left:60px;
	border-radius: 15px;
	@media #{$r414} {
		top:80px;
		bottom:auto;
	}
	
	&__tt{
		color:#f38b25;
		font-size:14px;
	}
	&__ct{
		color:#fff;
		font-size:17px;
		text-transform: uppercase;
	}
}
.pc{
	@media #{$r414}{
		display: none
	}
}
.mb{
	display: none;
	@media #{$r414}{
		display: block;
	}
}

.kq {
	background: #1A8065;
	display: block;		
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	color: #fff;
	z-index: 2;
	padding: 5px 0;
	@media #{$r768} {
		position: relative;
		left:auto;
		bottom:auto;
	}
	p {
		margin: 0;
	}
}
.pulse_regist {
	box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.7);
	-webkit-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
	-moz-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
	-ms-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
	animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
}
.boxRegist{
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
	-webkit-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 0);
	-moz-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 0);
	-ms-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 0);
	animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 0);
}
@-webkit-keyframes xoayvong{
    from{
        -webkit-transform:rotateY(0deg);
        -moz-transform:rotateY(0deg);
        -o-transform:rotateY(0deg);
    }
    to{
        -webkit-transform:rotateY(360deg);
        -moz-transform:rotateY(360deg);
        -o-transform:rotateY(360deg);
    }
}
/* Standard syntax */ 
@keyframes xoayvong {
    from{
        -webkit-transform:rotateY(0deg);
        -moz-transform:rotateY(0deg);
        -o-transform:rotateY(0deg);
    }
    to{
        -webkit-transform:rotateY(360deg);
        -moz-transform:rotateY(360deg);
        -o-transform:rotateY(360deg);
    }
}
.siteHeaderLogoXoay img{-webkit-animation:xoayvong 2s linear infinite;-moz-animation:xoayvong 2s linear infinite;-o-animation:xoayvong 2s linear infinite;-ms-animation:xoayvong 2s linear infinite;animation:xoayvong 2s linear infinite;}
@-webkit-keyframes won {
	to {
		box-shadow: 0 0 0 10px rgba(232, 76, 61, 0)
	}
}

@-moz-keyframes won {
	to {
		box-shadow: 0 0 0 10px rgba(232, 76, 61, 0)
	}
}

@-ms-keyframes won {
	to {
		box-shadow: 0 0 0 10px rgba(232, 76, 61, 0)
	}
}

@keyframes won {
	to {
		box-shadow: 0 0 0 10px rgba(232, 76, 61, 0)
	}
}
