    &Title_line{
        line-height: 1;
        img{
            width: 100%;
            max-width: 40%;
            @media #{$r414}{
                max-width: 80%;
            }
        }
    }
	&Sub {
		text-align: center;
		font-size: 18px;
		text-transform: uppercase;
		@media #{$r414}{
			font-size:12px;
		}
		@media #{$r320}{
			font-size:10px;
		}
	}

	&Line {
		background-repeat: no-repeat;
		width: 258px;
		border-top: 1px solid #d8d8d8;
		position: relative;

		@media #{$r768} {
			margin: 20px auto 30px;
		}

		&::before {
			width: 100px;
			margin: 0;
			border-top: 3px solid #f69679;
			position: absolute;

			top: -2px;
			content: "";

			@media #{$r768} {
				left: 76px;
			}
		}

		&-left {
			margin: 10px 0 30px;
			border:none;
			&::before {
				width:70px;
				left: 0;
			}
		}

		&-center {
			margin: 20px auto 30px;

			&::before {
				left: 80px;
			}
		}
	}

	&2,
	&3,
	&4 {
		position: relative;

		&:after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
		}

		&.loaded:after {
			opacity: 0;
		}
	}
}

